export default class NavComponent {
  constructor() {
    this.navDadClass = '.js-nav-dad';
    this.navItemClass = '.js-nav-item';
    this.navContentElement = '.js-nav-content';

    this.navBackDadElement = '.js-nav-dad-back';
    this.navBackElement = '.js-nav-back';

    this.activeNaModal = '';
  }

  handlerNavDad() {
    const navDadItem = document.querySelectorAll(this.navDadClass);

    [...navDadItem].map(dad => {
      dad.addEventListener('click', _event => {
        _event.preventDefault();

        const element = _event.target;
        
        this._activeNavModal(element);
      }); 
    });
  }

  handlerItem() {
    const navItem = document.querySelectorAll(this.navItemClass);
    
    [...navItem].map(nav => {
      nav.addEventListener('click', _event => {
        _event.preventDefault();

        const element = _event.target;
        
        const dataName = element.dataset.navName;

        this.activeNaModal = dataName;
        
        this._changeAllActivities(dataName);
      });
    });
  }

  _activeNavModal(el) {
    const { parentElement } = el;
    const elClassName = el.classList[0];
    const parentClassName = parentElement.parentElement.classList[0];

    this._setActiveClass(el, elClassName);
    this._setActiveClass(parentElement.parentElement, parentClassName);
  }

  _changeAllActivities(navName) {
    const navItemClass = document.querySelector(`[data-nav-name="${navName}"]`)
    const modalElement = document.querySelector(`[data-nav-content="${navName}"]`);

    const navItemInitialClass = navItemClass.classList[0];
    const modalElementInitialClass = modalElement.classList[0];

    this._setActiveClass(navItemClass, navItemInitialClass);
    this._setActiveClass(modalElement, modalElementInitialClass);
  }

  _setActiveClass(element, className) {
    element.classList.toggle(`${className}--active`);
  }

  backHandler() {
    const backNavElement = document.querySelectorAll(this.navBackElement);
    const backDadNavElement = document.querySelectorAll(this.navBackDadElement);

    [...backNavElement].map(element => {
      element.addEventListener('click', _event => {
        _event.preventDefault();
        
        this._changeAllActivities(this.activeNaModal);
      });
    });
    
    [...backDadNavElement].map(element => {
      element.addEventListener('click', _event => {
        const parentsElement = _event.target.parentElement.parentElement.parentElement.parentElement;

        const parentsClassName = parentsElement.classList[1];
        const parentsSiblings = _event.target.parentElement.parentElement.previousElementSibling;
        const parentsSiblingsActiveClass = parentsSiblings.classList[2];
        
        parentsSiblings.classList.remove(parentsSiblingsActiveClass);
        parentsElement.classList.remove(parentsClassName);
      });
    });
  }

  load() {
    this.handlerNavDad();
    this.handlerItem();
    this.backHandler();
  }
}