import NavComponent from './../../general/components/b8one.navComponent';
import LoginComponent from './../../general/components/b8one.loginComponent';
import CartService from 'Services/b8one.cartService.js';

class General {
  constructor() {
    this.nav_ = new NavComponent();
    this.login_ = new LoginComponent();

    this.cartService = new CartService();

    this.eventHandlerBinds();
  }

  loadscreen() {
    const loadScreenElement = document.querySelector('.js-loadscreen');
    const loadScreenBarElement = document.querySelector('.js-bar-status');

    let barProgress = 1;

    const setProgress = setInterval(() => {
      if (barProgress >= 100) {
        clearInterval(setProgress);

        loadScreenElement.classList.add('loadscreen--disabled');
      } else {
        barProgress++;
        loadScreenBarElement.style.width = barProgress + '%';
      }
    }, 25);
  }

  accordion() {
    $('.js-footer-expand').click(function(e) {
      $(this)
        .next()
        .slideToggle();
    });
  }

  eventHandlerBinds() {
    // Page's DOM content is loaded
    document.addEventListener('DOMContentLoaded', event => {
      // this.loadscreen();
      // this.accordion();
      this.headerSticky();
      this.componentBuilder();
      this.setHandledButtonPageBackClick();
      this.menuActions();
      this.addCSSPropertyVH();
      this.setHandledWindowResize();
      this.cancelAction();

      this.setHandledInitSaveCustomDataEvent();
      this.setHandledEndSaveCustomDataEvent();
    });
  }

  componentBuilder() {
    this.login_.load();
    this.nav_.load();

    this.createInitSaveCustomDataEvent();
    this.createEndSaveCustomDataEvent();
  }

  handledButtonPageBackClick() {
    const URLs = {
      'empresa-de-mudanca': '/mudanca',
      endereco: '/empresa-de-mudanca',
      'seguro-info': '/endereco',
      seguro: '/seguro-info',
      resumo: '/seguro',
    };

    const { pathname } = window.location;

    const key = pathname.replace('/', '');

    window.location.assign(URLs[key]);
  }

  setHandledButtonPageBackClick() {
    const buttonPageBack = document.querySelector('.js-button-page-back');

    buttonPageBack &&
      buttonPageBack.addEventListener('click', e =>
        this.handledButtonPageBackClick()
      );
  }

  menuActions() {
    const headerButtonMenu = document.querySelector('.header__button-menu');
    const headerWrapperMenu = document.querySelector('.header__wrapper--menu');

    const toogleMenu = () => {
      const icon = headerButtonMenu.children[0];

      if (icon.classList.contains('fa-bars')) {
        icon.classList.replace('fa-bars', 'fa-times');
        document.body.classList.add('without-scroll');
        headerWrapperMenu.classList.add('header__wrapper--menu-show');
      } else {
        icon.classList.replace('fa-times', 'fa-bars');
        document.body.classList.remove('without-scroll');
        headerWrapperMenu.classList.remove('header__wrapper--menu-show');
      }
    };

    headerButtonMenu.addEventListener('click', toogleMenu);

    const navigationLinks = document.querySelectorAll(
      '.navigation__list > .navigation__link'
    );

    [...navigationLinks].forEach(link => {
      link.addEventListener('click', event => {
        toogleMenu();
      });
    });

    const subMenus = document.querySelectorAll('.navigation__list .list__item');

    [...subMenus].forEach(subMenu => {
      const subList = subMenu.querySelector('.navigation__sublist');
      const navigationLink = subMenu.querySelector('.navigation__link');

      navigationLink.addEventListener('click', ({ target }) => {
        subList && subList.classList.add('navigation__sublist--show');
      });

      const buttonBack = subList.querySelector('.header__button-back');

      buttonBack.addEventListener('click', event => {
        subList && subList.classList.remove('navigation__sublist--show');
      });
    });
  }

  cancelAction() {
    const tooltip = document.querySelector('button.tooltip');

    tooltip &&
      tooltip.addEventListener('click', e => {
        e.preventDefault();
      });
  }

  /*
    This function adds a custom css property to fix the viewport unit problem in mobile.
    see more in https://css-tricks.com/the-trick-to-viewport-units-on-mobile/
  */
  addCSSPropertyVH() {
    // First we get the viewport height and we multiple it by 1% to get a value for a vh unit
    let vh = window.innerHeight * 0.01;

    // Then we set the value in the --vh custom property to the root of the document
    document.documentElement.style.setProperty('--vh', `${vh}px`);
  }

  setHandledWindowResize() {
    window.addEventListener('resize', event => {
      this.addCSSPropertyVH();
    });
  }

  headerSticky() {
    const header = document.querySelector('.header');

    window.addEventListener(
      'scroll',
      e => {
        const scrollElement =
          document.scrollingElement || document.documentElement;

        if (scrollElement.scrollTop >= 20) {
          header.classList.add('header--sticky');
        } else {
          header.classList.remove('header--sticky');
        }
      },
      true
    );
  }

  createInitSaveCustomDataEvent() {
    const initSaveCustomDataEvent = document.createEvent('Event');

    initSaveCustomDataEvent.initEvent('initSaveCustomData', true, true);

    window.initSaveCustomDataEvent = initSaveCustomDataEvent;
  }

  createEndSaveCustomDataEvent() {
    const endSaveCustomDataEvent = document.createEvent('Event');

    endSaveCustomDataEvent.initEvent('endSaveCustomData', true, true);

    window.endSaveCustomDataEvent = endSaveCustomDataEvent;
  }

  handledInitSaveCustomDataEvent() {
    const buttonSaveIcon = document.querySelector('.js-button-save');

    if (buttonSaveIcon) {
      buttonSaveIcon.classList.add('fa-pulse');
      buttonSaveIcon.classList.replace('fa-save', 'fa-spinner-third');
    }
  }

  handledEndSaveCustomDataEvent() {
    const buttonSaveIcon = document.querySelector('.js-button-save');

    if (buttonSaveIcon) {
      buttonSaveIcon.classList.remove('fa-pulse');
      buttonSaveIcon.classList.replace('fa-spinner-third', 'fa-check-circle');

      setTimeout(() => {
        buttonSaveIcon.classList.remove('button--success');
        buttonSaveIcon.classList.replace('fa-check-circle', 'fa-save');
      }, 1000);
    }
  }

  setHandledInitSaveCustomDataEvent() {
    document.addEventListener(
      'initSaveCustomData',
      this.handledInitSaveCustomDataEvent
    );
  }

  setHandledEndSaveCustomDataEvent() {
    document.addEventListener(
      'endSaveCustomData',
      this.handledEndSaveCustomDataEvent
    );
  }
}

new General();
