/* eslint-disable max-len, no-undef */
import Component from './b8one.component';
import $ from 'jquery';
// import VtexUser from '../services/b8one.vtexUser';

/**
 * LoginComponent Abstraction class
 */
class LoginComponent extends Component {
  /**
   * constructor
   */
  constructor() {
    super();

    this.VTEX_LOGOUT_URL = '/no-cache/user/logout';
    this.VTEX_ACCOUNT_URL = '/_secure/account#/profile';
    this.VTEX_ORDERS_URL = '/_secure/account#/orders';
    this.LOGOUT_LABEL = 'Sair';
    this.isUserDefined = false;
    this.isMobile = window.innerWidth < 1024;
    // this._vtexUser = new VtexUser();
    this.loginContainerSelector = '.js-login--container';
    this.loginActionSelector = '.js-login--action';
  }

  /**
   * load component
   */
  load() {
    this.render();
    this.eventHandlerBinds();
  }

  /**
   * Component's render method
   */
  async render() {
    const loginButtonElement = this.getElement(this.loginContainerSelector);

    if (loginButtonElement) {
      // this._vtexUser.getCurrent().then((user) => {
      const user = await $.get('/no-cache/profileSystem/getProfile');
      const loginActionElement = loginButtonElement.querySelector(
        this.loginActionSelector
      );

      this.isUserDefined = user.IsUserDefined;

      console.log(this.isUserDefined);

      if (this.isUserDefined) {
        console.log('loginActionElement', loginActionElement);

        if (loginActionElement) {
          console.log('loginButtonElement', loginButtonElement);

          if (!this.isMobile) {
            loginActionElement.innerHTML = user.FirstName
              ? `Olá, ${user.FirstName}`
              : `Olá`;
          }

          // <li class="User__Item" ><a class="User__Link" href="/mudanca?showMyLists=true">Minhas listas</a></li>

          loginButtonElement.insertAdjacentHTML(
            'beforeend',
            `
            <ul class="User__Dropdown">  
              <li class="User__Item" ><a class="User__Link" href="${this.VTEX_ACCOUNT_URL}">Minha Conta</a></li>
              <li class="User__Item" ><a class="User__Link" href="${this.VTEX_ORDERS_URL}">Minhas mudanças</a></li>
              <li class="User__Item" ><a class="User__Link" href="${this.VTEX_LOGOUT_URL}">Sair</a></li>
            </ul>
          `
          );

          if (!this.isMobile) {
            loginButtonElement.href = this.VTEX_ACCOUNT_URL;
          }
        }
      }
    }
  }

  /**
   * Open user login modal
   */
  userLogin() {
    vtexid.start({
      locale: this.props.language,
      forceReload: true,
    });
  }

  /**
   * Get User's display name
   * @param {*} userObj - Vtex User JSON Object
   * @return {String} User's display name
   */
  getUserDisplayName(userObj) {
    return Ensure.isUndefined(userObj.FirstName)
      ? userObj.Email.split('@')[0].split('.')[0]
      : userObj.FirstName;
  }

  /**
   * Event binds
   */
  eventHandlerBinds() {
    // loginButton.addEventListener('click', (event) => {
    //   _this.userLogin();
    // }, false);
    const this_ = this;
    const loginButtonElement = this.getElement(this.loginContainerSelector);

    if (loginButtonElement) {
      loginButtonElement.addEventListener(
        'click',
        event => {
          // event.preventDefault();

          if (!this.isUserDefined) {
            this_.userLogin();
          }
        },
        false
      );
    }
  }
}

export default LoginComponent;
